.navigationBar {
  height: 80px;
  padding: 0 15px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 60px;

  box-shadow: rgb(158, 158, 158) 0px 4px 10px 4px;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
  z-index: 10;
}

.navigationItem {
  padding: 0 7px;
  text-decoration: none;
  font-size: 18px;
  font-weight: 600;
  color: #121417;
}

.navigationItem:hover,
.navigationItem:focus {
  color: #0b44cd;
}
