.buttonFavorite {
  width: 18px;
  height: 18px;
  position: absolute;
  top: 14px;
  right: 14px;
  padding: 0;
  background: none;
}

.buttonFavorite:hover,
.buttonFavorite:focus {
  background: none;
}

.buttonFavorite:hover {
  scale: 1.1;
}

.carImage {
  width: 274px;
  height: 268px;
  margin-bottom: 14px;
  border-radius: 14px;
  object-fit: cover;
}

.carCaption {
  display: flex;
  justify-content: space-between;
  margin-bottom: 8px;
  color: #121417;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  white-space: nowrap;
  overflow: hidden;
}

.carModel {
  color: #3470ff;
}

.carPrice {
  flex-shrink: 1;
}

.carDetailsList {
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
  white-space: nowrap;
  overflow: hidden;
  color: rgba(18, 20, 23, 0.5);
}

.carDetailsFirstLine {
  margin-bottom: 4px;
}

.carDetailsSecondLine {
  margin-bottom: 28px;
}

.buttonLearnMore {
  width: 100%;
  height: 44px;
}
